'use client';

import { useState, useEffect, FormEvent, ChangeEvent } from 'react';
import axios from '@/lib/axios';
import Image from 'next/image';
import { toast } from 'sonner';
import { Plus, Pencil, Trash2, Loader2, MapPin, Clock, DollarSign } from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Switch } from '@/components/ui/switch';
import {
    Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogTrigger, DialogDescription
} from '@/components/ui/dialog';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';

import { Lapangan } from '@/types';

export default function LapanganPage() {
    const [lapangans, setLapangans] = useState<Lapangan[]>([]);
    const [loading, setLoading] = useState(true);
    
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isSaving, setIsSaving] = useState(false);
    const [editId, setEditId] = useState<number | null>(null);

    const [formData, setFormData] = useState({
        nama_lapangan: '',
        deskripsi: '',
        harga_per_jam: '',
        jam_buka: '08:00:00', 
        jam_tutup: '22:00:00',
        status_aktif: true,
        foto: null as File | null,
    });

    // --- FETCH DATA ---
    const fetchLapangans = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/lapangans');
            setLapangans(response.data);
        } catch (error) {
            toast.error("Gagal memuat data lapangan.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchLapangans(); }, []);

    // Helper Format Rupiah
    const formatRupiah = (num: number | string) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));

    // --- HANDLERS ---
    const handleOpenDialog = (item?: Lapangan) => {
        if (item) {
            // Edit Mode
            setEditId(item.id);
            setFormData({
                nama_lapangan: item.nama_lapangan,
                deskripsi: item.deskripsi || '',
                harga_per_jam: item.harga_per_jam.toString(),
                jam_buka: item.jam_buka,
                jam_tutup: item.jam_tutup,
                status_aktif: Boolean(item.status_aktif),
                foto: null
            });
        } else {
            // Create Mode
            setEditId(null);
            setFormData({
                nama_lapangan: '', deskripsi: '', harga_per_jam: '', 
                jam_buka: '08:00:00', jam_tutup: '22:00:00', status_aktif: true, foto: null
            });
        }
        setIsDialogOpen(true);
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Hapus lapangan ini? Data booking terkait mungkin akan error.")) return;
        try {
            await axios.delete(`/api/lapangans/${id}`);
            toast.success("Lapangan dihapus.");
            fetchLapangans();
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal menghapus.");
        }
    };

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);

        try {
            if (editId) {
                await axios.put(`/api/lapangans/${editId}`, {
                    nama_lapangan: formData.nama_lapangan,
                    deskripsi: formData.deskripsi,
                    harga_per_jam: formData.harga_per_jam,
                    jam_buka: formData.jam_buka,
                    jam_tutup: formData.jam_tutup,
                    status_aktif: formData.status_aktif
                });

                if (formData.foto) {
                    const imgData = new FormData();
                    imgData.append('foto', formData.foto);
                    await axios.post(`/api/lapangans/${editId}/foto`, imgData);
                }
                toast.success("Lapangan diperbarui!");
            } else {
                // CREATE (FormData Gabungan)
                const data = new FormData();
                data.append('nama_lapangan', formData.nama_lapangan);
                data.append('deskripsi', formData.deskripsi);
                data.append('harga_per_jam', formData.harga_per_jam);
                data.append('jam_buka', formData.jam_buka);
                data.append('jam_tutup', formData.jam_tutup);
                data.append('status_aktif', formData.status_aktif ? '1' : '0');
                if (formData.foto) data.append('foto', formData.foto);

                await axios.post('/api/lapangans', data);
                toast.success("Lapangan ditambahkan!");
            }
            setIsDialogOpen(false);
            fetchLapangans();
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal menyimpan.");
        } finally {
            setIsSaving(false);
        }
    };

    return (
        <div className="space-y-6">
            {/* Header */}
            <div className="flex justify-between items-center">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Data Lapangan</h1>
                    <p className="text-gray-500 text-sm">Kelola fasilitas, harga, dan jam operasional.</p>
                </div>
                <Button className="bg-[#D93F21] hover:bg-[#b9351b]" onClick={() => handleOpenDialog()}>
                    <Plus className="w-4 h-4 mr-2" /> Tambah Lapangan
                </Button>
            </div>

            {/* Grid Lapangan (Card View lebih cocok daripada tabel untuk lapangan) */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {loading ? (
                    <p>Memuat data...</p>
                ) : lapangans.length === 0 ? (
                    <p className="text-gray-500">Belum ada data lapangan.</p>
                ) : (
                    lapangans.map((item) => (
                        <Card key={item.id} className="overflow-hidden group hover:shadow-lg transition-all duration-300">
                            <div className="relative h-48 w-full bg-gray-200">
                                {item.foto_url ? (
                                    <Image 
                                        src={item.foto_url} 
                                        alt={item.nama_lapangan} 
                                        fill 
                                        className="object-cover group-hover:scale-105 transition-transform duration-500"
                                        unoptimized
                                    />
                                ) : <div className="flex items-center justify-center h-full text-gray-400">No Image</div>}
                                
                                <div className="absolute top-2 right-2">
                                    {item.status_aktif ? (
                                        <Badge className="bg-green-500 hover:bg-green-600">Aktif</Badge>
                                    ) : (
                                        <Badge className="bg-red-500 hover:bg-red-600 text-white">Renovasi/Tutup</Badge>
                                    )}
                                </div>
                            </div>
                            
                            <CardContent className="p-5 space-y-4">
                                <div>
                                    <h3 className="font-bold text-xl text-gray-900 flex items-center gap-2">
                                        <MapPin className="w-5 h-5 text-[#D93F21]" /> {item.nama_lapangan}
                                    </h3>
                                    <p className="text-sm text-gray-500 line-clamp-2 mt-1">
                                        {item.deskripsi || 'Tidak ada deskripsi.'}
                                    </p>
                                </div>

                                <div className="flex justify-between items-center text-sm border-t pt-4 border-gray-100">
                                    <div className="flex items-center gap-2 text-gray-600">
                                        <Clock className="w-4 h-4" />
                                        <span>{item.jam_buka} - {item.jam_tutup}</span>
                                    </div>
                                    <div className="font-bold text-[#D93F21] text-lg">
                                        {formatRupiah(item.harga_per_jam)} <span className="text-xs text-gray-400 font-normal">/jam</span>
                                    </div>
                                </div>

                                <div className="flex gap-2 pt-2">
                                    <Button variant="outline" className="flex-1 border-gray-200 hover:border-blue-300 hover:text-blue-600" onClick={() => handleOpenDialog(item)}>
                                        <Pencil className="w-4 h-4 mr-2" /> Edit
                                    </Button>
                                    <Button variant="outline" className="flex-1 border-gray-200 hover:border-red-300 hover:text-red-600" onClick={() => handleDelete(item.id)}>
                                        <Trash2 className="w-4 h-4 mr-2" /> Hapus
                                    </Button>
                                </div>
                            </CardContent>
                        </Card>
                    ))
                )}
            </div>

            {/* Dialog Modal Form */}
            <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
                <DialogContent className="sm:max-w-[500px]">
                    <DialogHeader>
                        <DialogTitle>{editId ? 'Edit Lapangan' : 'Tambah Lapangan Baru'}</DialogTitle>
                        <DialogDescription>Informasi ini akan tampil di halaman depan pelanggan.</DialogDescription>
                    </DialogHeader>

                    <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                        <div className="space-y-2">
                            <Label>Nama Lapangan <span className="text-red-500">*</span></Label>
                            <Input value={formData.nama_lapangan} onChange={e => setFormData({...formData, nama_lapangan: e.target.value})} required placeholder="Cth: Lapangan A (Indoor)" />
                        </div>

                        <div className="grid grid-cols-2 gap-4">
                            <div className="space-y-2">
                                <Label>Harga per Jam (Rp) <span className="text-red-500">*</span></Label>
                                <div className="relative">
                                    <DollarSign className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
                                    <Input type="number" className="pl-9" value={formData.harga_per_jam} onChange={e => setFormData({...formData, harga_per_jam: e.target.value})} required />
                                </div>
                            </div>
                            <div className="space-y-2 pt-6">
                                <div className="flex items-center space-x-2">
                                    <Switch id="status" checked={formData.status_aktif} onCheckedChange={c => setFormData({...formData, status_aktif: c})} />
                                    <Label htmlFor="status">Status Aktif</Label>
                                </div>
                            </div>
                        </div>

                        <div className="grid grid-cols-2 gap-4">
                            <div className="space-y-2">
                                <Label>Jam Buka</Label>
                                <Input type="time" step="1" value={formData.jam_buka} onChange={e => setFormData({...formData, jam_buka: e.target.value})} required />
                            </div>
                            <div className="space-y-2">
                                <Label>Jam Tutup</Label>
                                <Input type="time" step="1" value={formData.jam_tutup} onChange={e => setFormData({...formData, jam_tutup: e.target.value})} required />
                            </div>
                        </div>

                        <div className="space-y-2">
                            <Label>Deskripsi</Label>
                            <Textarea value={formData.deskripsi} onChange={e => setFormData({...formData, deskripsi: e.target.value})} placeholder="Fasilitas: Lantai kayu, AC, Tribun..." />
                        </div>

                        <div className="space-y-2">
                            <Label>Foto Lapangan</Label>
                            <Input type="file" accept="image/*" className="cursor-pointer" onChange={e => { if(e.target.files?.[0]) setFormData({...formData, foto: e.target.files[0]}) }} />
                        </div>

                        <DialogFooter className="pt-4">
                            <Button type="button" variant="outline" onClick={() => setIsDialogOpen(false)}>Batal</Button>
                            <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                                {isSaving ? <Loader2 className="h-4 w-4 animate-spin" /> : 'Simpan'}
                            </Button>
                        </DialogFooter>
                    </form>
                </DialogContent>
            </Dialog>
        </div>
    );
}